.ExpandablePanel {
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.18), 0 0 0 0.0625rem rgba(0, 0, 0, 0.08);
  padding: 2rem;
  margin-bottom: 2rem;
}

.ExpandablePanel__summary {
  cursor: pointer;
  vertical-align: center;
}

.ExpandablePanel__summaryContent {
  font-size: 1.5rem;
  display: inline-block;
  margin: 0;
  margin-bottom: 1rem;
  padding-left: 0.5rem;
}